/* Styles go in this file. */

html, body { height: 100%; margin: 0; -webkit-font-smoothing: antialiased; }

#search-box {
	font-family: inherit;
	box-sizing: border-box;
	height: 2rem;
	font-size: 1rem;
	border-radius: 3px;
	border: 1px solid rgba(0,0,0,0.2);
	padding-left: 0.25rem;
	margin: 0 0 5px 0;
	max-width: 100%;
}
footer {
	.flourish-footer-note:empty {
		margin: 0;
	}
}
#table-container {
	overflow: auto;
}
#table {
	display: table;
	width: 100%;
	border-collapse: collapse;
	box-sizing: border-box;
	&.sorting {
		#header-row .td {
			padding-right: 15px !important;
			&.sorted {
				position: relative;
				&:before {
					position: absolute;
					right: 5px;
					display: block;
					font-size: 12px;
					top: 50%;
					transform: translateY(-50%);
				}
				&.ascending:before {
					content: "▲";
				}
				&.descending:before {
					content: "▼";
				}
			}
		}
	}
	#header-row {
		font-weight: bold;
	}
	.tr {
		display: table-row;
	}
	.td {
		display: table-cell;
		border: none;
		overflow: hidden;
		cursor: default;
		line-height: 1.25em;
		position: relative;
		svg { display: block; }
		img {
			max-height: 100%;
			display: block;
		}
		> * {
			margin: 0;
			display: inline-block;
		}
		p {
			display: inline-block;
		}
	}
	&.hidden {
		display: none;
	}
}

.cell-popup {
	> * {
		margin: 0;
		display: inline-block;
	}
}

.flourish-popup-content {
	pointer-events: none !important;
	* {
		margin: 0;
	}
}

#edge {
    width: 1px;
    position: absolute;
    height: 100%;
    top: 0;
    right: 0;
    box-shadow: -1px 0px 4px rgba(0,0,0,0.3);
}

#pagination {
	text-align: center;
	font-size: 14px;
	* { display: inline-block }
	.pagination-btn {
		background: #888;
		color: #fff;
		font-family: sans-serif;
		border: none;
		font-size: 14px;
		font-weight: bold;
		border-radius: 3px;
		height: 24px;
		width: 21px;
		cursor: pointer;
		&.disabled {
			pointer-events: none;
			opacity: 0.2;
		}
	}
	input {
		height: 24px;
		box-sizing: border-box;
		font-size: 14px;
		font-family: inherit;
		width: 30px;
		text-align: center;
	}
	p {
		margin: 10px 20px;
	}
}

#table.blocks {
	#header-row { display: none };
	.tr {
		padding: 10px 0;
	}
	.tr, .td { display: block; height: auto !important; };
	.td:before {
		content: attr(data-header);
		display: block;
		font-weight: bold;
	}
}

#no-results {
	text-align: center;
	margin: 1em 0;
	opacity: 0.5;
	&.hidden {
		display: none;
	}
}